import Link from "next/link";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from "@/components/ui/carousel";
import { StatusIndicator } from "./status-indicator";
import type { Pillar } from "@/types";
import { getPillarStatus } from "@/lib/data";
import { Button } from "../ui/button";

export function CarouselPillarCard({ pillar }: { pillar: Pillar }) {
  const status = getPillarStatus(pillar.id);

  return (
    <Card className="flex flex-col justify-between h-full">
      <CardHeader>
        <div className="flex items-start justify-between">
          <div className="mb-4">
            <pillar.icon className="h-8 w-8 text-accent" />
          </div>
          <Link href={`/pillar/${pillar.id}`} passHref>
             <Button variant="outline" size="sm">View Details</Button>
          </Link>
        </div>
        <CardTitle>{pillar.name}</CardTitle>
        <CardDescription className="line-clamp-2">
          {pillar.description}
        </CardDescription>
      </CardHeader>
      <CardContent>
        <Carousel
          opts={{
            align: "start",
          }}
          className="w-full"
        >
          <CarouselContent>
            {pillar.subItems.map((item) => (
              <CarouselItem key={item.id} className="md:basis-1/2 lg:basis-1/3">
                <div className="p-1">
                  <Card>
                    <CardContent className="flex flex-col items-start gap-2 p-4">
                      <span className="text-sm font-semibold">{item.name}</span>
                      <StatusIndicator status={item.status} />
                      {item.ytd !== undefined && (
                        <div className="text-2xl font-bold">
                          {item.ytd}
                          <span className="text-xs text-muted-foreground"> YTD</span>
                        </div>
                      )}
                    </CardContent>
                  </Card>
                </div>
              </CarouselItem>
            ))}
          </CarouselContent>
          <CarouselPrevious className="hidden sm:flex" />
          <CarouselNext  className="hidden sm:flex"/>
        </Carousel>
      </CardContent>
      <CardFooter>
        <StatusIndicator status={status} />
      </CardFooter>
    </Card>
  );
}
